import React from 'react';
import { navigate } from 'gatsby';

import Button from './Button';
import GetDemoButton from './GetDemoButton';

import { screen } from '../constants';

export default function BlockCTA({
  pretitle = '',
  title = '',
  primaryButtonTo = '/',
  primaryButtonText = '',
  secondaryButtonTo = '/',
  secondaryButtonText = '',
  align = 'left',
}) {
  function handleSecondaryButtonTextClick() {
    navigate(secondaryButtonTo);
  }

  return (
    <section className="block-cta">
      <div>
        {pretitle && <div className="app__pretitle">{pretitle}</div>}
        {title && <h1 className="app__title">{title}</h1>}
        <div className="app__actions">
          {primaryButtonText && <GetDemoButton text={primaryButtonText} />}
          {secondaryButtonText && (
            <Button large onClick={handleSecondaryButtonTextClick}>
              {secondaryButtonText}
            </Button>
          )}
        </div>
      </div>
      <style jsx>{`
        .block-cta {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: flex-start;
          text-align: ${align};
        }
        .block-cta > div {
          flex: 1;
        }
        .block-cta .app__desc,
        .block-cta .app__title {
          max-width: 800px;
          ${align === 'center' ? 'margin: 0 auto;' : ''}
        }
        @media (max-width: ${screen.tabletMax}px) {
          .block-cta > div {
            flex: initial;
            margin: 0;
          }
          .block-cta {
            flex-direction: column-reverse;
          }
          .app__title {
            line-height: 1.35;
          }
        }
      `}</style>
    </section>
  );
}
